<template >
	<div class="box1"  >
			
		<div class="swiper-container" :class='swipeid'>
    		<div class="swiper-wrapper">
		        <slot name='bannerImg1'></slot>
				
		    </div>
    		<div :class='{"swiper-pagination":pagination}' :style="{'text-align':paginationDirection}"></div>
   	 	</div>
			
	</div>
</template>

<script>
	import "../../assets/js/swiper.js";
	
export default {
  
props:{
      swipeid:{
        type:String,
        default:''
      },
      loop:{		//
        type:Boolean,
        default:true
      },
      direction:{	//
        type:String,
        default:'horizontal'//  	vertical 
      },
      autoplay:{	//
        type:Number,
        default:5000
      },
      effect:{
      	default:'slide'	// slide   "cube"   		"coverflow"	"flip" 	fade
      },
      paginationType:{	//
        type:String,
        default:'bullets'	//bullets  fraction   progress    custom 
      },
      pagination:{		//
        type:Boolean,
        default:true
      },
      paginationDirection:{		//
        type:String,
        default:'center'
      },
      speed:{
      	default:1000
      },				//
    },
    mounted:function(){
      var _this=this;
      new Swiper('.'+_this.swipeid,{
        
        loop:_this.loop,
        
        pagination:".swiper-pagination",
        
        direction:_this.direction,
        
        autoplay:_this.autoplay,
        effect:_this.effect,	//slide  "cube"   		"coverflow"	"flip" 	fade		
        speed:_this.speed,				//
  		//autoplayDisableOnInteraction:false,	//swiperautoplaytrue
  		grabCursor:true,	//trueSwiper
  		freeMode : false,	//
  		freeModeMomentumVelocityRatio:2,	//
  		freeModeSticky:true,	//
  		freeModeMomentumBounce:true,	//
  		freeModeMomentumBounceRatio:3,	//
  		zoom:true,
      });
    }
  }

</script>

<style scoped="">
	@import "../../assets/css/swiper.css";
	
	.swiper-container img{height:2.39rem;width:100%;}
</style> 



