<template >
	<div class="box1"  >
			
		<div class="swiper-container" :class='swipeid'>
    		<div class="swiper-wrapper">
		        <slot name='bannerImg1'></slot>
		        <!--<div class="swiper-slide" slot="bannerImg1" >	
					<div class="swiper-zoom-container">
						<img src="../../assets/img1/banner/01.jpg"/>
					</div>	
				</div>
				<div class="swiper-slide" slot="bannerImg1" >
					<div class="swiper-zoom-container">
						<img src="../../assets/img1/banner/02.jpg"/>
					</div>
				</div>
				<div class="swiper-slide" slot="bannerImg1" >
					<div class="swiper-zoom-container">
						<img src="../../assets/img1/banner/03.jpg"/>
					</div>
				</div>-->
				
		    </div>
    		<div :class='{"swiper-pagination":pagination}' :style="{'text-align':paginationDirection}"></div>
   	 	</div>
			
	</div>
</template>

<script>
	import "../../assets/js/swiper.js";
	
export default {
  
props:{
	  swipeid:{		//
	type:String,
	default:''
	  },
	  loop:{		//
	    type:Boolean,
	    default:true
	  },
	direction:{	//
	type:String,
	default:'horizontal'//  	vertical 
	  },
	autoplay:{	//
	    type:Number,
	    default:5000
	  },
	effect:{
	  default:'slide'	// slide   "cube"   		"coverflow"	"flip" 	fade
	},
	paginationType:{	//
	type:String,
	default:'bullets'	//bullets  fraction   progress    custom 
	  },
	  pagination:{		//
	    type:Boolean,
	    default:true
	  },
	  paginationDirection:{		//
	type:String,
	default:'left'
	  },
	speed:{				//
	  	default:1000
	},				
	centeredSlides:{	//true 
		default:false
	},
	slidesPerView :{	//
		type:Number,
		default:1
	},
	spaceBetween:{			//
		default:20
	},
	autoplayDisableOnInteraction:{	//swiperautoplaytrue
		default:true
	},
	freeModeSticky:{		//
		default:false	
	},
	freeMode:{		//,truefreeslide
		default:true
	},
	freeModeMomentum:{	//falseslide
		default:true
	},
	freeModeMomentumVelocityRatio:{	//
		default:1
	},
	freeModeMomentumBounce:{		//
		default:false
	},
	freeModeMomentumBounceRatio:{		//
		default:0
	}
	
    },
    mounted:function(){
      var _this=this;
      new Swiper('.'+_this.swipeid,{
        centeredSlides:_this.centeredSlides,
        slidesPerView:_this.slidesPerView,
        spaceBetween:_this.spaceBetween,
        loop:_this.loop,
        pagination:".swiper-pagination",
        direction:_this.direction,
        autoplay:_this.autoplay,
        effect:_this.effect,			
        speed:_this.speed,				//
  		autoplayDisableOnInteraction:_this.autoplayDisableOnInteraction,	
  		freeModeSticky:_this.freeModeSticky,
  		freeMode : _this.freeMode,	
  		freeModeMomentum:_this.freeModeMomentum,
  		freeModeMomentumVelocityRatio:_this.freeModeMomentumVelocityRatio,	
  		freeModeMomentumBounce:_this.freeModeMomentumBounce,	
  		freeModeMomentumBounceRatio:_this.freeModeMomentumBounce,	
  		zoom:true,	//
  		grabCursor:true,	//trueSwiper
      });
    }
  }

</script>

<style  >
	@import "../../assets/css/swiper.css";
	
	 
</style> 



