<template >
	<div class="shouye" >
	<sousuo> </sousuo>
	<div class="zhanwei">
		
	</div>
	<lunbo swipeid='swipeid01' :autoplay=1000>
		<div class="swiper-slide" slot="bannerImg1" >	
			<div class="swiper-zoom-container">
				<img src="../../assets/img1/banner/01.jpg"/>
			</div>	
		</div>
		<div class="swiper-slide" slot="bannerImg1" >
			<div class="swiper-zoom-container">
				<img src="../../assets/img1/banner/02.jpg"/>
			</div>
		</div>
		<div class="swiper-slide" slot="bannerImg1" >
			<div class="swiper-zoom-container">
				<img src="../../assets/img1/banner/03.jpg"/>
			</div>
		</div>
</lunbo>

	<hotspot></hotspot>
		<hotspotwrap>
			<h2 slot='h2'> {{ title[0] }}  </h2>
			<p slot='p'> {{ nei[0] }}  </p>
			<div slot='line'></div>
			<span slot='author'>{{  zuozhe[0] }} </span>
			<i slot='line'></i>
			<img src='../../assets/img1/4_03.jpg' slot='img'/>
		</hotspotwrap>
		<hotspotwrap>
			<h2 slot='h2'>  {{ title[1] }} </h2>
			<p slot='p'>{{ nei[1] }}</p>
			<div slot='line'></div>
			<span slot='author'>{{  zuozhe[1] }}</span>
			<i slot='line'></i>
			<p slot='comeFrom'>{{ lanm[1] }} </p>
			<img src='../../assets/img1/4_06.jpg' slot='img'/>
		</hotspotwrap>
		<hotspotwrap>
			<h2 slot='h2'>14</h2>
			<p slot='p'>.</p>
			<div slot='line'></div>
			<span slot='author'></span>
			<i slot='line'></i>
			<p slot='comeFrom'></p>
			<img src='../../assets/img1/4_03.jpg' slot='img'/>
		</hotspotwrap>

	</div>
</template>


<script>
	import sousuo from"../../components/sousuo"
	import lunbo from"./lunbo"
	import hotspot from './hotspot'
	import hotspotwrap from './hotspotwrap'
    	
export default {
	data:function (){
	    return {
	      title: [
	      "14",
	      "|",
	      ],
	      nei: [
	      ".",
	      "....",
	      ],
	      zuozhe:["",""],

	      lanm:["0",""]
	    }
	 },
	components:{
		sousuo,
		lunbo,
		hotspot:hotspot,
	    hotspotwrap:hotspotwrap
	},
	methods:{
		fn:function(){
			//this.$emit("dianjii",this.xiab)
		}
	}
}

</script>

<style>
	.zhanwei{height: 1.25rem;width: 100%;}
	.shouye{background: #f7f7f7; padding-bottom: .95rem; }
</style> 



